.heading_style{
  color : #e30914;
  background-color: #000;
  text-align: center;
  font-family: 'railway', sans-serif;
  font-size: 50px;
  letter-spacing: 1px;
  font-weight: 500;
  margin-bottom: 30px;
  padding: 30px 0;
  font-family: 'Montserrat', sans-serif;
}

*{
  box-sizing: border-box;
  padding: 0;
  margin : 0;
  background-color: #d2dbdd;
}

.cards{
  width: 100%;
  height:auto;
}

.card{
  margin: 2% 6% 4% 6%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  background-color: #fff;
  width : 21.25%;
  border-radius: 12px;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0);
  float: left;
}

.card.hover{
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05,1.05);
}

.card__img { 
  width:100%;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card__info{
  background-color: #fff;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  padding: 16px 24px 24px 24px;
  background-color: #fff;
  text-align: center;
}

.card__category{
  font-family: "Railway",sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
}

.card_title{
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: "Railway",sans-serif;
  background-color: #fff;
  text-transform: capitalize;
}

button{
  padding: 5px 8px;
  font-family: "Railway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color:#fff;
  outline: none;
  border: 1px solid black;
  cursor: pointer;
}

